/**
 * Heads-up display displaying performance metrics
 * @packageDocumentation
 */

import type { Activate, FeatureContext } from '@grammarly/feature'
import { createInjectionToken, Lifetime } from '@grammarly/util-dependency-container'
import { pipe } from '@grammarly/util-function'
import { isDev } from '@grammarly/util-is-dev'
import { Result, ResultDisposable } from '@grammarly/util-result'
import { createElement } from 'react'
import { HUD as HUDComponent } from './components/HUD'
import { DefaultHUD } from './DefaultHUD'
import type { HUD } from './HUD'

export type { CreateValueOptions, HUD, HUDValue } from './HUD'

import type {} from '@grammarly/feature-ui'

/**
 * Token to inject HUD.
 * @public
 */
export const HUDToken = createInjectionToken<HUD>(Symbol('HUD'))

/** @public */
export const activate: Activate = context =>
  ResultDisposable.all([registerDependencies(context), showHUD(context)])

function registerDependencies(context: FeatureContext) {
  return pipe(
    context.getExecutionScope('global'),
    Result.flatMap(scope =>
      ResultDisposable.all([
        scope.register(
          {
            token: DefaultHUD,
            useFactory: () => {
              const hud = new DefaultHUD()

              let enabled = isDev
              if (isDev && !('HUD' in globalThis)) {
                Object.defineProperty(globalThis, 'HUD', {
                  get: () => {
                    try {
                      return Boolean(JSON.parse(localStorage.getItem('HUD') || 'true'))
                    } catch {
                      return enabled
                    }
                  },
                  set: (value: boolean) => {
                    try {
                      localStorage.setItem('HUD', JSON.stringify(value))
                    } catch {
                      enabled = value
                    }
                    hud.setEnabled(value)
                  },
                })
              }

              if (Reflect.get(globalThis, 'HUD')) {
                hud.setEnabled(true)
              }

              return Result.success(hud)
            },
          },
          {
            lifetime: Lifetime.Singleton,
          },
        ),
        scope.register({ token: HUDToken, useToken: DefaultHUD }),
      ]),
    ),
  )
}

function showHUD(context: FeatureContext) {
  return pipe(
    Result.all([context.getExecutionScope('global'), context.getFeature('ui')] as const),
    Result.map(([scope, ui]) =>
      scope.onStart(async execution =>
        pipe(
          Result.all([
            execution.resolve(ui.provides.MainViewContainer),
            execution.resolve(DefaultHUD),
          ] as const),
          Result.flatMap(([container, hud]) =>
            ResultDisposable.all([
              Result.success(
                container.add(
                  () =>
                    createElement(HUDComponent, {
                      hud,
                      style: {
                        display: 'inline-block',
                        position: 'fixed',
                        top: 8,
                        right: 8,
                        zIndex: Number.MAX_SAFE_INTEGER,
                      },
                    }),
                  { slot: 'default' },
                ),
              ),
            ]),
          ),
        ),
      ),
    ),
  )
}
