import { Text } from '@grammarly/design-system'
import { pipe } from '@grammarly/util-function'
import { useCallback, useEffect, useState } from 'react'
import type { CustomValue, HUDInternal } from '../DefaultHUD'
import type { DocumentID } from '../HUD'
import { ValuesList } from './ValuesList'

/** @internal */
export interface HUDProps {
  readonly hud: HUDInternal
  readonly style: React.CSSProperties
}

/** @internal */
export const HUD: React.FC<HUDProps> = ({ hud, style }) => {
  const [expanded, setExpanded] = useState(true)
  const [enabled, setEnabled] = useState(false)
  const [activeDocumentId, setActiveDocumentId] = useState<DocumentID | null>(null)

  // null is used for entries that have no associated document
  const [customValues, setCustomValues] = useState<
    ReadonlyMap<DocumentID | null, readonly CustomValue[]>
  >(new Map())

  useEffect(() => {
    const disposables = new DisposableStack()

    disposables.use(hud.enabled.subscribe(setEnabled))
    disposables.use(hud.activeDocumentId.subscribe(setActiveDocumentId))
    disposables.use(hud.customValues.subscribe(setCustomValues))

    return () => disposables.dispose()
  }, [])

  const onClick = useCallback(() => setExpanded(expanded => !expanded), [setExpanded])

  return (
    enabled && (
      <div
        data-name="HUD"
        title={activeDocumentId ?? undefined}
        onClick={onClick}
        style={{
          ...style,
          padding: 4,
          backgroundColor: 'rgba(255, 255, 255, 0.5)',
          transform: 'scale(0.75)',
          transformOrigin: 'top right',
          borderRadius: 4,
          backdropFilter: 'blur(4px)',
          border: '1px solid #ccc',
          boxSizing: 'content-box',
        }}
      >
        <Text as="strong" variant="text-xsmall">
          {activeDocumentId?.slice(0, 30)}
        </Text>
        {expanded &&
          pipe(
            customValues.get(null),
            values => values && <ValuesList values={values} key="root" />,
          )}
        {expanded &&
          pipe(
            customValues.get(activeDocumentId),
            values => values && <ValuesList values={values} key="active-document" />,
          )}
      </div>
    )
  )
}
