import { Text, type TextProps } from '@grammarly/design-system'
import type { SubscribableState } from '@grammarly/util-reactive'
import { useEffect, useState } from 'react'

export interface ReactiveTextProps {
  readonly variant?: TextProps['variant']
  readonly text: SubscribableState<
    string | { readonly text: string; readonly color: TextProps['color'] }
  >
}

export const ReactiveText: React.FC<ReactiveTextProps> = props => {
  const [text, setText] = useState<string>('')
  const [color, setColor] = useState<TextProps['color']>('base-default')

  useEffect(() => {
    const disposables = new DisposableStack()
    disposables.use(
      props.text.subscribe(value => {
        if (typeof value === 'string') {
          setText(value)
        } else {
          setText(value.text)
          setColor(value.color)
        }
      }),
    )
    return () => disposables.dispose()
  }, [setText])

  return (
    <Text as="span" variant={props.variant} color={color}>
      {text}
    </Text>
  )
}
