import { Flex, Text } from '@grammarly/design-system'
import { pipe } from '@grammarly/util-function'
import { groupBy } from '@grammarly/util-iterable'
import { SubscribableState } from '@grammarly/util-reactive'
import type React from 'react'
import type { CustomValue } from '../DefaultHUD'
import { ReactiveText } from './ReactiveText'

export interface ValuesListProps {
  readonly values: readonly CustomValue[]
}

export const ValuesList: React.FC<ValuesListProps> = ({ values }) => {
  const sections = Array.from(groupBy(values, item => item.section).entries())

  return sections.map(([section, values], idx) => (
    <Flex direction="column" key={idx}>
      {section && (
        <Text as="span" variant="text-xsmall" marginTop={1} weight="medium">
          {section}
        </Text>
      )}
      {values.map((value, idx) => (
        <Flex key={idx} align="center" justify="space-between" direction="row" gap={1}>
          <Text as="span" variant="text-xsmall">
            {value.label}
          </Text>
          <Flex>
            <ReactiveText
              variant="text-xsmall"
              text={pipe(
                value.source,
                SubscribableState.map(x => ({
                  text:
                    x === undefined || x === null
                      ? ''
                      : typeof x === 'number'
                        ? Number.isInteger(x)
                          ? x.toString()
                          : x.toFixed(1)
                        : value.stringify
                          ? value.stringify(x)
                          : String(x),
                  color: value.getColorFor ? value.getColorFor(x) : undefined,
                })),
              )}
            />
            {value.unit && (
              <Text as="span" variant="text-xsmall">
                {value.unit}
              </Text>
            )}
          </Flex>
        </Flex>
      ))}
    </Flex>
  ))
}
