import { State, SubscribableState } from '@grammarly/util-reactive'
import type { CreateValueOptions, DocumentID, HUD, HUDValue } from './HUD'

/** @internal */
export type CustomValue<T = unknown> = CreateValueOptions<T> & {
  readonly source: SubscribableState<T | undefined>
}

/** @internal */
export interface HUDInternal extends HUD {
  readonly enabled: SubscribableState<boolean>
  readonly activeDocumentId: SubscribableState<DocumentID | null>
  readonly customValues: SubscribableState<ReadonlyMap<DocumentID | null, readonly CustomValue[]>>
}

/** @internal */
export class DefaultHUD implements HUDInternal, Disposable {
  readonly #disposables = new DisposableStack()
  readonly #enabled = new State(false)
  readonly #activeDocumentId = new State<DocumentID | null>(null)
  readonly #customValues = new State<ReadonlyMap<DocumentID | null, readonly CustomValue[]>>(
    new Map(),
  )

  readonly enabled = this.#enabled
  readonly activeDocumentId = this.#activeDocumentId
  readonly customValues = this.#customValues

  setEnabled(enabled: boolean) {
    this.#enabled.setValue(enabled)
  }

  setActiveDocument(documentId: DocumentID | null): void {
    this.#activeDocumentId.setValue(documentId)
  }

  createValue<T>(options: CreateValueOptions<T>): HUDValue<T> {
    const state = new State<T | undefined>(undefined)
    const customValue: CustomValue<T> = {
      ...options,
      source: state,
    }
    const docId = (options.documentId ?? null) as DocumentID | null
    const customValues = [
      ...(this.#customValues.getValue().get(docId) ?? []),
      customValue as unknown as CustomValue,
    ].sort((a, b) => (a.section ?? '').localeCompare(b.section ?? ''))

    this.#customValues.update(values => {
      const newValues = new Map(values)
      return newValues.set(docId, customValues)
    })

    return {
      push: value => state.setValue(value),
      [Symbol.dispose]: () => {
        const customValues = (this.#customValues.getValue().get(docId) ?? []).filter(
          item => item !== customValue,
        )
        this.#customValues.update(values => {
          const newValues = new Map(values)
          if (customValues.length === 0) {
            newValues.delete(docId)
          } else {
            newValues.set(docId, customValues)
          }
          return newValues
        })
      },
    }
  }

  [Symbol.dispose]() {
    this.#disposables.dispose()
  }
}
